.autocomplete {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 4px;
  position: absolute;
  max-width: 210px;
  width: 100%;
  left: 39px;
  top: 40px;
  z-index: 10;
  max-height: 307.74px;
  height: auto;
  gap: 4px;
  background: #fff;
  filter: drop-shadow(0px 0px 7px rgba(0,0,0,0.25));
}
.autocomplete::before {
  top: -10.25px;
  left: 17px;
  transform: rotate(90deg);
  content: "";
  position: absolute;
  background: url("../../images/decorate.svg") no-repeat;
  width: 24px;
  height: 24px;
  z-index: 200;
}
.autocomplete__wrap {
  padding: 0;
  margin: 6px 4px;
  height: 100%;
  overflow-y: auto;
}
.autocomplete__search {
  display: block;
  padding: 0;
  margin: 0;
  list-style: none;
  margin-right: 4px;
  margin-block-start: 0;
}
.autocomplete__result {
  display: flex;
  flex-direction: column;
  row-gap: 6px;
  width: 190px;
}
.autocomplete__result:hover {
  background: #27a1ff;
  color: #fff;
}
.autocomplete__text-menu-title {
  padding: 0;
  margin: 0;
  margin: 4px 9px 0;
  height: 16px;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
}
.autocomplete__text {
  padding: 0;
  margin: 0;
  height: 16px;
  margin: 0 9px 4px;
/* font-menu-tile */
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 14px;
}
* {
  scrollbar-width: thin;
}
*::-webkit-scrollbar {
  height: 20px;
  width: 6px;
}
*::-webkit-scrollbar-track {
  margin-top: 7px;
  margin-bottom: 7px;
  height: 20px;
  background: inherit;
}
*::-webkit-scrollbar-thumb {
  background-color: #c4c4c4;
  border-radius: 3px;
  max-height: 120px;
}
